<template>
  <div class="out_box">
  	<el-container class="show_outbox">
      <el-main class="customization">
      	<div class="website_icon icon1"></div>
				<h2 class="title">OA</h2>
				<div class="title">Office Automation,OAOA</div>
				<img src="/static/images/OA.jpg" class="banner" alt=""> 
	    	<div class="website_icon icon1"></div>
				<h2 class="title"></h2>
				<div class="title">ERPHRSCMCRM</div>
				<img src="/static/images/OA.jpg" class="banner" alt="">
      </el-main> 
    </el-container>
  </div>
</template>

<script>
export default {
  name: 'Customization',
  data(){
  	return{}
  }
}
</script>

<style lang="less" scoped>
	.out_box{
		width: 100%;
		.customization{
			margin: 0 auto;
			padding: 80px 20px;
			.website_icon{
				width: 48px;
				height: 48px;
				margin: 0 auto;
			}
			.icon1{
				background: url('/static/images/goodimg.png');
			}
			h2.title{
				font-size: 0.4rem;
				line-height: 0.4rem;
				padding-top: 10px;
			}
			div.title{
				width: 600px;
				margin: 0 auto;
				padding:20px 0 60px 0;
				color: #999;
				font-size: 0.14rem;
				line-height: 0.24rem;
			}
			.banner{
				padding: 20px 0;
			}
		}
	}
@media only screen and (max-width: 767px){
	.out_box{
		.customization{
			padding: 40px 20px;
			h2.title{
				font-size: 0.24rem;
				line-height: 0.24rem;
			}
			div.title{
				width: 100%;
				padding: 20px 0;
			}
			.banner{
				width: 100%;
			}
		}
	}
}	
	
</style>



