<script>
export default {
  name: 'Branch',
  data(){
    return{
      msg:''
    }
  }
}
</script>

<style lang="less" scoped="">
.branch_box{
	h2.title{
		font-size: 0.32rem;
		line-height: 0.42rem;
		padding-bottom: 10px;
		color: rgb(100, 100, 100);
	}
	.text{
		text-align: left;
		p{
			color: #999999;
			&.time{
				font-size: 0.14rem;
				line-height: 0.2rem;
				padding: 10px 0;
			}
			&.subtitle{
				font-size: 0.14rem;
				line-height: 0.24rem;
			}
		}
	}
	.show_item{
		img{
			width: 100%;
			display: block;
			padding: 15px 0;
		}
		.title{
			font-size: 0.14rem;
			line-height: 0.2rem;
			color: #999;
			padding-left: 10px;
			text-align: left;
		}
	}
}
</style>



