<template>
  <div>
    <el-container class="show_outbox">
      <el-main class="show_box">
      	<h1 class="title"></h1>
	    <p class="subtitle"><br/>
N</p>
      	 <el-row class="show_content">
	        <el-col :xs="18" :sm="18" :md="18" :lg="18" :xl="18" :offset="3" v-for="(it,index) in list" :key="index" class="show_item">
	          	<div class="img_box">
	          		<img :src="it.src" alt="">
	          	</div>
	    		<p class="title">{{it.title}}</p>
	    		<div>{{it.html}}</div>
	        </el-col>
	      </el-row>
      </el-main> 
    </el-container>
  </div>
</template>

<script>
export default {
  name: 'Introduction',
  data(){
    return{
      list:[
      	{
      		src:"/static/images/about/trnl-01.png",
      		title:"",
      		html:"APP"
		},
      	{
      		src:"/static/images/about/trnl2-01.png",
      		title:"",
      		html:""
      	},
      	{
      		src:"/static/images/about/trnl3-01.png",
      		title:"",
      		html:""
      	}
      ]
    }
  }
}
</script>

<style lang="less" scoped>
.show_box{
	padding:50px 20px;
	h1.title{
		font-size: 0.24rem;
		line-height: 0.3rem;
		padding-bottom: 10px
	}
	p.subtitle{
		line-height: 0.26rem;
	    margin-bottom: 40px;
	    color: rgb(155, 163, 175);
	    font-size: 0.15rem;
	}
	.show_content{
		.show_item{
			padding:20px 0 40px;
			div{
				line-height: 0.20rem;
			    font-size: 0.16rem;
			    color: rgb(155, 163, 175);
			}
			p.title{
				line-height: 50px;
				font-size: 0.2rem;
			}
			.img_box{
				padding: 0 50px;
				img{
					width: 100%;
					display: block;
				}
			}
			@media only screen and (max-width: 767px){
				.img_box{
					padding: 0;
				}
			}
		}
	}
}

</style>



