<template>
  <div class="company_box">
    <el-container class="show_outbox">
      <el-main class="show_box">
      	<h2 class="title"></h2>
      	<div class="text">
	      	<p class="time">&nbsp;&nbsp;&nbsp;&nbsp;400-9918-302</p>
	      	<p class="subtitle">&nbsp;&nbsp;&nbsp;&nbsp;</p>
      	</div>
      	<el-row class="show_content">
	        <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" class="show_item" v-for="(it,index) in list" :key="index">	
	        	<img :src="it.src" alt="" />
	        	<p class="title">{{it.html}}</p>
	        </el-col>
	      </el-row>
      </el-main> 
    </el-container>
  </div>
</template>

<script>
export default {
  name: 'Company',
  data(){
    return{
      list:[
      	{
      		src:"/static/images/connection/1-16051G00S0914.jpg",
      		html:""
      	},
      	{
      		src:"/static/images/connection/1-16042QGUH39.jpg",
      		html:""
      	},
      	{
      		src:"/static/images/connection/1-16042QI243109.jpg",
      		html:""
      	},
      	{
      		src:"/static/images/connection/1-16042P95623257.png",
      		html:""
      	}
      ]
    }
  }
}
</script>

<style lang="less" scoped="">
.company_box{
	h2.title{
		font-size: 0.32rem;
		line-height: 0.42rem;
		padding-bottom: 10px;
		color: rgb(100, 100, 100);
	}
	.text{
		text-align: left;
		p{
			color: #999999;
			&.time{
				font-size: 0.14rem;
				line-height: 0.2rem;
				padding: 10px 0;
			}
			&.subtitle{
				font-size: 0.14rem;
				line-height: 0.24rem;
			}
		}
	}
	.show_item{
		img{
			width: 100%;
			display: block;
			padding: 15px 0;
		}
		.title{
			font-size: 0.14rem;
			line-height: 0.2rem;
			color: #999;
			padding-left: 10px;
			text-align: left;
		}
	}
}
</style>



