<template>
  <div class="out_box">
  	<el-container class="show_outbox">
      <el-main class="wechat_box">
    		<el-row class="show_content" >
	        <el-col  :xs="24" :sm="24" :md="24" :lg="24" :xl="24" v-for="(it,index) in list" :key="index" class="show_item">	        		
	    			<h2>{{it.title}}</h2>
	    			<p>{{it.html}}</p>
	    			<img :src="it.src" alt="" class="icon" />
	        </el-col>
      	</el-row>
      </el-main> 
  	</el-container>
  </div>
</template>

<script>
export default {
  name: 'Wechat',
  data(){
  	return{
  		list:[
  			{
  				src:"/static/images/1-160425201KH47.png",
  				title:"",
  				html:""
  			},
  			{
  				src:"/static/images/comwebsite2-01.png",
  				title:"",
  				html:"PC"
  			},
  			{
  				src:"/static/images/comwebsite4-01.png",
  				title:"B2C",
  				html:"B2CB2CB2CB2CB2C"
  			}
  		]
  	}
  }
}
</script>

<style lang="less" scoped>
	.out_box{
		width: 100%;
		.wechat_box{
			margin: 0 auto;
			text-align: left;
			.show_item{
				padding-bottom:60px;
				h2{
					font-size: 0.30rem;
					line-height: 0.4rem;
				}
				p{
					color: #999;
					font-size: 0.14rem;
					line-height: 0.24rem;
					padding: 40px 0;
				}
				img{
					width: 100%;
					display: block;
				}
			}
		}
	}
	@media only screen and (max-width: 767px){
	.out_box{
		.wechat_box{
			.show_item{
				padding-bottom:40px;
			}
		}
	}
}
</style>



