<template>
  <div class="out_box">
  	<el-container class="show_outbox">
      <el-main class="show_box">
      	<h1 class="title"></h1>
	    	<p class="subtitle"></p>
      	 <el-row class="show_content">
	        <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8"  v-for="(it,index) in list" :key="index" class="show_item">
	          <div class="website_icon" :class="it.icon"></div>
		    		<p class="title">{{it.title}}</p>
		    		<div>{{it.html}}</div>
	        </el-col>
	      </el-row>
	      <img src="/static/images/com123.jpg" class="banner" alt="">   
      </el-main> 
    </el-container>
    <div class="content_bg">
	    <el-container class="show_outbox">
	      <el-main class="show_box">
	      	<h1 class="title"></h1>
		    	<p class="subtitle"></p>
	      	 <el-row class="show_content">
		        <el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12"  v-for="(it,index) in list2" :key="index" class="show_item">
		          <div class="website_icon" :class="it.icon"></div>
			    		<p class="title">{{it.title}}</p>
			    		<div>{{it.html}}</div>
		        </el-col>
		      </el-row>
		      <img src="/static/images/menhu123.jpg" class="banner" alt="">
	      </el-main> 
	    </el-container>  
    </div>
    <el-container class="show_outbox">
      <el-main class="show_box">
      	<h1 class="title"></h1>
	    	<p class="subtitle">,</p>
      	 <el-row class="show_content">
	        <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8"  v-for="(it,index) in list3" :key="index" class="show_item">
	          <div class="website_icon" :class="it.icon"></div>
		    		<p class="title">{{it.title}}</p>
		    		<div>{{it.html}}</div>
	        </el-col>
	      </el-row>
	      <img src="/static/images/zhengfu123.jpg" class="banner" alt="">   
      </el-main> 
    </el-container>  	       
  </div>
</template>

<script>
export default {
  name: 'Website',
  data(){
  	return{
  		list:[
  			{
  				icon:"icon1",
  				title:"",
  				html:""
  			},
  			{
  				icon:"icon2",
  				title:"",
  				html:""
  			},
  			{
  				icon:"icon3",
  				title:"",
  				html:"PC"
  			}
  		],
  		list2:[
  			{
  				icon:"icon4",
  				title:"",
  				html:""
  			},
  			{
  				icon:"icon5 website_icon2",
  				title:"",
  				html:""
  			}
  		],
  		list3:[
  			{
  				icon:"icon6 website_icon3",
  				title:"",
  				html:""
  			},
  			{
  				icon:"icon7",
  				title:"",
  				html:""
  			},
  			{
  				icon:"icon8 website_icon4",
  				title:"",
  				html:""
  			}
  		]
  	}
  }
}
</script>


<style lang="less" scoped>	
.show_box{
	padding:80px 20px;
	h1.title{
		font-size: 0.36rem;
		line-height: 60px;
	}
	p.subtitle{
		font-size: 0.2rem;
		color: #999;
		line-height: 30px;
		padding-bottom: 20px;
	}
	.banner{
		width: 100%;
		display: block;
		margin-top: 30px;
	}
	.show_content{
		.show_item{
			padding:20px;
			div{
				line-height: 0.23rem;
				font-size: 0.14rem;
				color: #999;
			}
			p.title{
				line-height: 50px;
				font-size: 0.2rem;
			}
			.website_icon{
				width: 48px;
				height: 48px;
				margin: 0 auto;
				&.icon1{
					background: url('/static/images/goodimg.png');
				}
				&.icon2{
					background: url('/static/images/goodimg.png') no-repeat;
					background-position: -49px 0;
				}
				&.icon3{
					background: url('/static/images/goodimg.png') no-repeat;
					background-position: -98px 0;
				}
				&.icon7{
					background: url('/static/images/goodimg.png') no-repeat;
					background-position: -405px -48px;
				}
				&.icon4{
					background: url('/static/images/goodimg.png') no-repeat;
					background-position: 0 -49px;
				}
			}
			.website_icon2{
				width: 62px;
				height: 48px;
				margin: 0 auto;
				&.icon5{
					background: url('/static/images/goodimg.png') no-repeat;
					background-position: -147px 0;
				}
			}
			.website_icon3{
				width: 39px;
				height: 48px;
				margin: 0 auto;
				&.icon6{
					background: url('/static/images/goodimg.png') no-repeat;
					background-position: -281px -48px;
				}
			}
			.website_icon4{
				width: 33px;
				height: 48px;
				margin: 0 auto;
				&.icon8{
					background: url('/static/images/goodimg.png') no-repeat;
					background-position: -320px -48px;
				}
			}
		}
	}
}
.content_bg{
	width: 100%;
	background: #f2f2f2;
}
@media only screen and (max-width: 767px){
	.show_box{
		padding:30px 20px;
	}
}
</style>



